.work_showcase {
	background-color: #FFFFFF;
}

.showcase_top {
	padding: 68px 0 16px 0;
}

.showcase_title {
	max-width: 345px;
	color: var(--Text-Primary, #262626);
	padding-bottom: 26px;

	/* Heading/h2 */
	font-family: Unbounded;
	font-size: 36px;
	font-style: normal;
	font-weight: 500;
	line-height: 110%; /* 39.6px */
}

.showcase_filter {
	display: flex;
	flex-wrap: wrap; // —â–æ–± –∫–Ω–æ–ø–∫–∏ –ø–µ—Ä–µ–Ω–æ—Å–∏–ª–∏—Å—å –Ω–∞ –Ω–æ–≤–∏–π —Ä—è–¥–æ–∫ –Ω–∞ –º–∞–ª–µ–Ω—å–∫–∏—Ö –µ–∫—Ä–∞–Ω–∞—Ö
}

.filter-btn {
	padding: 10px 15px;
	margin-right: 5px; /* —Ç—ñ–ª—å–∫–∏ –≥–æ—Ä–∏–∑–æ–Ω—Ç–∞–ª—å–Ω–∏–π –ø—Ä–æ–º—ñ–∂–æ–∫ */
	margin-bottom: 10px; /* –≤–µ—Ä—Ç–∏–∫–∞–ª—å–Ω–∏–π –≤—ñ–¥—Å—Ç—É–ø –Ω—É–ª—å */
	margin-top: 0;

	font-family: 'Montserrat', sans-serif;
	font-size: 14px;
	font-style: normal;
	font-weight: 700;
	line-height: 100%; /* 14px */
	border: none; // —á–æ—Ä–Ω–∞ —Ä–∞–º–∫–∞
	border-radius: 9999px; // –ø–æ–≤–Ω—ñ—Å—Ç—é –æ–∫—Ä—É–≥–ª—ñ –∫—Ä–∞—ó
	background-color: transparent;
	color: #000;
	cursor: pointer;
	transition: all 0.4s ease-in-out;

	&.active {
		background-color: #000;
		color: #fff;
		border-color: #000;
	}

	&:hover {
		background-color: #000;
		color: #fff;
	}
}


.showcase_slide {
	display: flex;
	max-width: 891px!important;
	height: 600px!important;
	padding: 24px;
	flex-direction: column;
	align-items: flex-start;
	gap: 24px;


	border-radius: 24px;
	background: var(--Body-Light-Grey, #F9F9F9);

	.showcase_slide__title {
		display: grid;
		gap: 4px;
		padding-bottom: 26px;

		.title_top {
			color: var(--Text-Secondary, #434343);

			/* Body/med 16px */
			font-family: Montserrat;
			font-size: 16px;
			font-style: normal;
			font-weight: 500;
			line-height: 140%; /* 22.4px */
		}

		.title_bottom {
			color: var(--Text-Primary, #262626);

			/* Heading/h4 */
			font-family: Unbounded;
			font-size: 22px;
			font-style: normal;
			font-weight: 500;
			line-height: 110%; /* 24.2px */
		}
	}


	.showcase_slide_img {
		display: flex;
		border-radius: 16px;
		width: 100%;
		overflow: hidden; /* –≤–∞–∂–ª–∏–≤–æ */
		max-height: 478px;
		height: 100%;
	}
}

.showcase_swiper {
	position: relative!important;
	width: 100vw!important; // –Ω–∞ –≤—Å—é —à–∏—Ä–∏–Ω—É –µ–∫—Ä–∞–Ω–∞
	margin-left: calc(50% - 50vw) !important; // –∑—Å—É–Ω—É—Ç–∏, —â–æ–± –≤–∏—Ä—ñ–≤–Ω—è—Ç–∏ –∑ –µ–∫—Ä–∞–Ω–æ–º
	padding-left: calc((100vw - 1200px) / 2) !important; // —Å—Ç–∞—Ä—Ç –∑–ª—ñ–≤–∞ —è–∫ —É .container
	overflow: hidden!important;
	padding-bottom: 106px!important;

	.swiper-wrapper {
		display: flex;
	}

	.swiper-slide {
		flex-shrink: 0;
	}

	.swiper-pagination {
		position: absolute;
		bottom: 70px!important;
		left: 0;
		width: 100% !important;
		display: flex!important;
		justify-content: center!important;
		gap: 12px;

		.swiper-pagination-bullet {
			width: 12px;
			height: 12px;
			border-radius: 50%;
			background: #959595;
			opacity: 0.4;
			position: relative;
			z-index: 1;
		}

		// –ê–∫—Ç–∏–≤–Ω–∏–π —ñ–Ω–¥–∏–∫–∞—Ç–æ—Ä
		&::after {
			content: '';
			position: absolute;
			top: 50%;
			height: 13px;

			border-radius: 6px;
			background: var(--Text-Secondary, #434343);
			transform: translateY(-50%);
			transition: all 0.15s ease-in-out;
			z-index: 2;

			/* –¥–∏–Ω–∞–º—ñ—á–Ω—ñ –∑–º—ñ–Ω–Ω—ñ */
			left: var(--bullet-left, 0);
			width: var(--bullet-width, 12px);
		}
	}


}


.showcase_bottom {
	background: var(--Body-Light-Grey, #F9F9F9);

	.showcase_bottom__title {
		padding: 59px 0 36px 0;
		color: var(--Text-Primary, #262626);

		font-family: 'Unbounded', sans-serif;
		font-size: 18px;
		font-style: normal;
		font-weight: 500;
		line-height: 110%; /* 19.8px */
	}

	.showcase_slide_bottom {
		height: auto;
		max-width: 120px;
	}


	.showcase_swiper_bottom {
		padding-bottom: 59px;
		width: 100vw; // –Ω–∞ –≤—Å—é —à–∏—Ä–∏–Ω—É –µ–∫—Ä–∞–Ω–∞
		margin-left: calc(50% - 50vw); // –≤–∏—Ä—ñ–≤–Ω—é–≤–∞–Ω–Ω—è –ø–æ –µ–∫—Ä–∞–Ω—É
		padding-left: calc((100vw - 1200px) / 2); // —Å—Ç–∞—Ä—Ç –≤—ñ–¥ –∫–æ–Ω—Ç–µ–π–Ω–µ—Ä–∞
		overflow: hidden;

		.swiper-slide {
			flex-shrink: 0;
			width: auto; // —â–æ–± –ø—ñ–¥–ª–∞—à—Ç–æ–≤—É–≤–∞–ª–æ—Å—å –ø—ñ–¥ –∫–æ–Ω—Ç–µ–Ω—Ç
		}
	}

}


@media (max-width: 1199px) {
	.showcase_top {
		padding: 48px 0 16px 0;
	}
}

@media (max-width: 768px) {
	.showcase_bottom .showcase_swiper_bottom{
		padding-bottom: 24px;
	}
	.showcase_bottom .showcase_bottom__title{
		padding: 24px 0 24px 0;
	}
	.showcase_swiper .swiper-wrapper{
		gap: 0;
	}
	.showcase_slide {
		height: 380px !important;
	}
	.showcase_slide_img {
		max-height: 250px;
	}
	.showcase_slide .showcase_slide_img{
		max-height: 77%;
	}
}
@media (max-width: 480px) {
	.showcase_swiper .swiper-pagination{
		bottom: 20px!important;
	}
	.showcase_swiper{
		padding-bottom: 48px!important;
	}
	.showcase_top {
		padding: 24px 0 16px 0;
	}
	.showcase_title{
		font-size: 30px;
		padding-bottom: 24px;

	}
}





