.btn {
	display: flex;                  /* flex –∑–∞–º—ñ—Å—Ç—å inline-block */
	align-items: center;            /* –≤–µ—Ä—Ç–∏–∫–∞–ª—å–Ω–µ –≤–∏—Ä—ñ–≤–Ω—é–≤–∞–Ω–Ω—è */
	justify-content: center;        /* –≥–æ—Ä–∏–∑–æ–Ω—Ç–∞–ª—å–Ω–µ –≤–∏—Ä—ñ–≤–Ω—é–≤–∞–Ω–Ω—è */
	padding: 20px 16px;
	max-width: 249px;
	width: 100%;
	border-radius: 32px;
	border: 2px solid var(--Body-Brand, #01D0CC);
	background: rgba(0,0,0,0);
	color: var(--Body-Brand, #01D0CC);
	font-family: "Montserrat", sans-serif;
	font-size: 16px;
	font-style: normal;
	font-weight: 700;
	line-height: 120%; /* —â–æ–± –ø–µ—Ä–µ–Ω–æ—Å –≤–∏–≥–ª—è–¥–∞–≤ –∫—Ä–∞—â–µ */
	text-decoration: none;
	transition: all .4s ease-in-out;
	text-align: center;

&:hover {
		background: #00ffff; // –ø—Ä–∏ —Ö–æ–≤–µ—Ä—ñ –∑–∞–ø–æ–≤–Ω—é—î–º–æ –∫–æ–ª—å–æ—Ä–æ–º
		color: #000; // –∫–æ–ª—ñ—Ä —Ç–µ–∫—Å—Ç—É –∑–º—ñ–Ω—é—î—Ç—å—Å—è
	}
}


.btn.mod {
	display: inline-block;
	padding: 13px 0;
	width: 100%; // –≤–∏—Å–æ—Ç–∞ —ñ —à–∏—Ä–∏–Ω–∞ –∫–Ω–æ–ø–∫–∏
	border-radius: 32px;
	border: none;
	background: white; // –ø—Ä–æ–∑–æ—Ä–∏–π —Ñ–æ–Ω

	/* Button/bold 16px */
	color: var(--Text-Primary, #262626);

	/* Button/bold 14px */
	font-family: 'Montserrat', sans-serif;
	font-size: 14px;
	font-style: normal;
	font-weight: 700;
	line-height: 100%; /* 14px */
	transition: all 0.4s ease-in-out;
	text-align: center;

	&:hover {
		background: #00ffff; // –ø—Ä–∏ —Ö–æ–≤–µ—Ä—ñ –∑–∞–ø–æ–≤–Ω—é—î–º–æ –∫–æ–ª—å–æ—Ä–æ–º
		color: #000; // –∫–æ–ª—ñ—Ä —Ç–µ–∫—Å—Ç—É –∑–º—ñ–Ω—é—î—Ç—å—Å—è
		opacity: 1;
	}
}
